@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Montserrat', sans-serif;
}

body{
    background-color: #c9d6ff;
    background: linear-gradient(to right, #e2e2e2, #c9d6ff);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 100vh;
}

.container{
    /* padding-top:30px; */
    background-color: #fff;
    border-radius: 30px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.35);
    position: relative;
    overflow: hidden;
    width: 768px;
    max-width: 100%;
    min-height: 480px;
}

.container p{
    font-size: 12px;
    line-height: 20px;
    letter-spacing: 0.3px;
    margin: 20px 0;
}

.container span{
    font-size: 12px;
    transform: translateX(-42%);
}

.container a{
    transform: translateY(36%);
    color: #FE8C00;
    font-size: 12px;
    text-decoration: none;
    margin: 0px 2px 0px;
}

.container .signin{
    background: #FE8C00;
    width:100%;
    color: #fff;
    font-size: 12px;
    padding: 10px;
    border: 1px solid transparent;
    border-radius: 100px;
    font-weight: 600;
    margin-top: 10px;
    cursor: pointer;
}

.container .signin.hidden{
    background-color: transparent;
    border-color: #fff;
}

.container .login{
    padding-top: 50px;
    padding-left:40px;
    padding-right:40px;
    padding-bottom:30px;
    z-index:1;
    border-radius: 0px 30px 30px 0px;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 100%;
}

.container form h3{
    transform: translateX(-9%);
    font-size: 25px;
    text-align: left;
    font-weight: 600;
    line-height: 40px;
}

.container input{
    background-color: #eee;
    border: none;
    margin: 8px 0;
    padding: 10px 15px;
    font-size: 13px;
    border-radius: 8px;
    width: 100%;
    outline: none;
}

.form-container{
    position: absolute;
    top: 0;
    height: 100%;
    transition: all 0.6s ease-in-out;
}


.sign-in{
    left: 0;
    width: 55%;
    z-index: 2;
}


@keyframes move{
    0%, 49.99%{
        opacity: 0;
        z-index: 1;
    }
    50%, 100%{
        opacity: 1;
        z-index: 5;
    }
}


.email{
    font-size: 15px;
    transform: translateX(-337%);
}
.password{
    font-size: 15px;
    transform: translateX(-178%);
}


.gsign{
    display:-webkit-inline-box;
    height:20px;
    width:300px;
}

.gsign div{
    height:1px;
    width:80px;
    position: relative;
    top:45%;
    border: 0.5px solid #878787
}

.gsign .signin{
    width:50px;
}

.icon{
    height:24px;
    width:24px;
}

.line1{
    left:15px;
}
.line2{
    left:35px;
}

.gsign span{
    position: relative;
    left:30%;
    bottom:25%;
    font-size: 12px;
}

.toggle-container{
    position: absolute;
    top: 0;
    left: 50%;
    width: 50%;
    height: 100%;
    overflow: hidden;
    transition: all 0.6s ease-in-out;
}

.burger{
    margin-top:-5px;
}