.container{
    z-index: 1;
}

.carouselExampleIndicators{
    position: relative;
}

.carousel-inner{
    transform: scale(140%);
    margin-top:-10px;
}

.main-content{
    padding:40px 60px 40px 60px; 
    position: absolute;
    background-color: #FE8C00;
    height:300px;
    width: 500px;
    top:50%;
    left:50%;
    transform: translate(-50%,-10%);
    z-index:2;
    border-radius:48px;
    text-align:center;
    color:white;
}

.headline{
    font-size:30px;
    margin-bottom:-10px;
}

.subtext{
    padding: 0px 40px 0px 40px;
    margin-top:-20px;
}

.carousel-item{
    transform: scale(100%);
}

.carousel-control-prev{
    z-index: 4;
    transform:translate(140px,370px);
}

.carousel-control-next{
    transform:translate(-120px,370px);
    z-index: 4;
}

.carousel-control-prev-icon{
    transform: translate(-20%);
}

/* .carousel-control-next-icon{

} */

.next{
    position: absolute;
    top:100%;
    left:50%;
    transform: translate(-50%,-35%);
    width:70px;
    height:70px;
    z-index: 5;
    background: #fff;
    color:#FE8C00;
}

.next:hover{
    position: absolute;
    top:100%;
    left:50%;
    transform: translate(-50%,-35%);
    width:70px;
    height:70px;
    z-index: 5;
    background: #f3f3f3;
    color:#FE8C00;
}


