
.form-check .form-check-input{
    margin-left:-2.5rem;
}

.form-check-input:checked{
    background-color: #f18400;
    border-color:#d87700
}

.container .register{
    padding-top: 30px;
    padding-left:40px;
    padding-right:40px;
    padding-bottom:30px;
    z-index:1;
    border-radius: 0px 30px 30px 0px;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 100%;
}

.container form h3{
    transform: translateX(0%);
}

h6{
    font-size:15px;
}

.form-container span{
    transform: translateX(0%);
    margin-top:-3px;
    margin-bottom:5px;
}

.form-container{
    position: absolute;
    top: 0;
    height: 100%;
    transition: all 0.6s ease-in-out;
}

.sign-up{
    left: 0;
    width: 55%;
    z-index: 2;
}

button{
    background: #FE8C00;
    width:100%;
    color: #fff;
    font-size: 12px;
    padding: 10px;
    border: 1px solid transparent;
    border-radius: 100px;
    font-weight: 600;
    margin-top: 10px;
    cursor: pointer;
}

button:hover{
    background: #f18400;
    width:100%;
    color: #fff;
    font-size: 12px;
    padding: 10px;
    border: 1px solid transparent;
    border-radius: 100px;
    font-weight: 600;
    margin-top: 10px;
    cursor: pointer;
}

button:hover:active{
    background: #d87700;
}

.username{
    margin-left:-15rem;
}

.email1{
    margin-left:-13.5rem;
}
.password1{
    margin-left:-15.5rem;
}

.toggle-container2{
    position: absolute;
    top: 0;
    left: 47%;
    width: 60%;
    height: 100%;
    overflow: hidden;
    transition: all 0.6s ease-in-out;
}

.burger2{
    z-index:-2;
    margin-left:2rem;
}

.accexist{
    margin:-30px 0px -20px 0px;
    display:flex;
    transform: translate(40%);
}

.privacy_policy{
    transform: translate(2%);
}