  .clock-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transform-origin: 0%;
    height:max-content;
    width:max-content;
    margin: 40px;
    transform: translate(40%,10%);
  }
  
  .clock{
    height:250px;
    width:auto;
    z-index:-10;
    transform: translate(-3%,92%);
  }

  svg {
    transform: rotate(180deg);
  }
  .center{
    background: white;
    border: 1px solid red;
    height:15px;
    width:15px;
    border-radius:100%;
    transform: translateY(-6.7rem)
  }  

  text{
    transform: rotate(180deg);
  }
  
  .time-display {
    font-size: 24px;
    font-weight: bold;
    margin-top: 20px;
  }

  .range{
    height: 50px;
    width:380px;
    background: #fff;
    border-radius: 10px;
    padding: 0 65px 0 45px;
    text-align:center;
    box-shadow: 2px 4px 8px rgba(0,0,0,0.1);
  }
  .sliderValue{
    position: relative;
    width: 100px;
  }
  .sliderValue span{
    position: absolute;
    height: 45px;
    width: 45px;
    transform: translateX(-70%) scale(0);
    font-weight: 500;
    top: -40px;
    line-height: 55px;
    z-index: 2;
    color: #fff;
    transform-origin: bottom;
    transition: transform 0.3s ease-in-out;
  }
  .sliderValue span.show{
    transform: translateX(-70%) scale(1);
  }
  .sliderValue span:after{
    position: absolute;
    content: '';
    height: 100%;
    width: 100%;
    background:#f18400 ;
    border: 3px solid #fff;
    z-index: -1;
    left: 50%;
    transform: translateX(-50%) rotate(45deg);
    border-bottom-left-radius: 50%;
    box-shadow: 0px 0px 8px rgba(0,0,0,0.1);
    border-top-left-radius: 50%;
    border-top-right-radius: 50%;
  }
  .field{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width:200px;
    position: relative;
  }
  .field .value{
    position: absolute;
    font-size: 18px;
    color: #f18400;
    font-weight: 600;
  }
  .field .value.left{
    left: -22px;
  }
  .field .value.right{
    right: -120px;
  }
  .range input{
    -webkit-appearance: none;
    width: 100%;
    height: 10px;
    background: #ddd;
    border-radius: 5px;
    outline: none;
    border: none;
    z-index: 22;
    transform: translate(20%);
  }
  .range input::-webkit-slider-thumb{
    -webkit-appearance: none;
    width: 20px;
    height: 20px;
    background: #f18400;
    border-radius: 50%;
    background: #f18400;
    border: 1px solid #f18400;
    cursor: pointer;
  }
  .range input::-moz-range-thumb{
    -webkit-appearance: none;
    width: 20px;
    height: 20px;
    background: #f18400;
    border-radius: 50%;
    background: #f18400;
    border: 1px solid #f18400;
    cursor: pointer;
  }
  .range input::-moz-range-progress{
    background: #f18400;
  }

